import React from 'react';
import s from './Posts.module.css';

const Posts = (props) => {
    return (
        <div className={s.posts}>
            <div className={s.item}>
                <img src="https://whatsism.com/uploads/posts/2018-07/1530544023_n6fgwzftnvg.jpg"/>
                {props.message}
                <div>
                    <span>like:</span>
                    {props.likesCount}
                </div>

            </div>
        </div>
    )
}

export default Posts;